.featuresWrapper {
  height: 100vh;
  width: 100%;
  display: flex;
  background-color: #282c34;
}

.sidebarContainer {
  height: max-content;
  width: 25%;
  padding-top: 50px;
}

.sidebarBTN {
  height: 200px;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 15px;
  font-size: 15px;
  background-color: #282c34;
  color: rgb(125, 169, 250);
  text-shadow: 2px 2px purple;
  border-top: 2px solid purple;
  border: none;
  font-family: 'Press Start 2P';
}

.sidebarBTN:hover {
  background: #464a55;
}

.featuresContainer {
  height: 100%;
  width: 100%;
}

.selectFeature {
  display: block;
  width: max-content;
  margin: 200px auto;
  font-family: 'Press Start 2P';
  color: rgb(138, 73, 138);
}

.featureSelected {
  display: none;
}
